@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,100italic,300italic);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #1C1E26;
  color: rgba(255, 255, 255, 1);
  font-family: "Roboto", Helvetica, sans-serif;
  font-size: 15pt;
  font-weight: 100;
  line-height: 2em;
}

@media screen and (max-width: 576px) {
  body {
    font-size: 15px;
    font-weight: 100;
    line-height: 2em;
  }
}

a {
  color: #E44C65;
  text-decoration: none;
  border-bottom: dotted 1px;
  transition: border-color 0.2s ease-in-out, color 0.2s ease-in-out;
}

a:hover {
  border-color: transparent;
}

p {
  margin-bottom: 2rem;
}

section {
  margin: 10vh 0;
}

.nav-link {
  color: #fff;
  text-decoration: none;
  border: none;
  margin: 1.8rem;
}

.nav-link:hover {
  color: #E44C65;
  text-decoration: none;
  border-bottom: dotted 1px;
}

/* responsive nav bar */
@media screen and (max-width: 576px) {
  .nav-link {
    padding: 0;
    margin: 0.5rem;
  }
}

.project-card-link {
  color: #fff;
  text-decoration: none;
  border: none;
  transition: color 0.2s ease-in-out;
}

.project-card-link:hover {
  color: #E44C65;
  text-decoration: none;
}


.content-wrapper {
  display: flex;
  flex-direction: column;
  width: 80vw;
  margin: auto;
}

.title {
  font-size: 2.5rem;
  margin-top: 3rem;
  margin-bottom: 3rem;
  line-height: 1.5;
}

@media screen and (max-width: 576px) {
  .title {
    font-size: 1.5rem;
  }
}
